<template>
  <div id="mySwiper">
    <div
      class="swiper"
      @touchstart="touchStart"
      @touchmove="touchMove"
      @touchend="touchEnd"
    >
      <slot></slot>
    </div>

    <div class="indicator">
      <div
        class="indicatorItem"
        v-for="(item, index) in slideCount"
        :key="index"
        :class="{ active: index === currentIndex - 1 }"
      ></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Swiper",
  props: {},
  data: function() {
    return {
      slideCount: 0,
      interval: 3000,
      imgWidth: 0, //swiper
      swiperStyle: {}, //swiperstyle
      currentIndex: 1, //
      scrolling: false, //,
      animalDuration: 300, //0.3
      showIndicator: true, //
      playTimer: null, //
      moveRatio: 0.25
    };
  },
  methods: {
    handleDom: function() {
      let swiperEle = document.querySelector(".swiper");
      let slideEles = document.getElementsByClassName("slide");
      this.slideCount = slideEles.length;
      console.log(this.slideCount);
      if (this.slideCount > 1) {
        let cloneFirst = slideEles[0].cloneNode(true);
        let cloneLast = slideEles[this.slideCount - 1].cloneNode(true);
        swiperEle.insertBefore(cloneLast, slideEles[0]);
        swiperEle.appendChild(cloneFirst);
        console.log(slideEles);
        this.imgWidth = swiperEle.offsetWidth;
        this.swiperStyle = swiperEle.style;
      }
      //
      this.setTransform(-this.imgWidth);
    },

    startTimer: function() {
      let that = this;
      this.playTimer = setInterval(function() {
        that.currentIndex++;
        that.scrollContent(-that.currentIndex * that.imgWidth); //
      }, that.interval);
    },

    setTransform: function(movePosition) {
      //
      this.swiperStyle.transform = `translateX(${movePosition}px)`;
      this.swiperStyle["-webkit-transform"] = `translateX(${movePosition}px)`;
      this.swiperStyle["-ms-transform"] = `translateX(${movePosition}px)`;
    },

    scrollContent: function(position) {
      this.scrolling = true; //
      this.swiperStyle.transition = `transform ${this.animalDuration}ms ease`;
      this.setTransform(position);
      this.checkPosition();
      this.scrolling = false;
    },
    checkPosition: function() {
      //

      let that = this;
      setTimeout(function() {
        that.swiperStyle.transition = "0ms";
        if (that.currentIndex >= that.slideCount + 1) {
          that.currentIndex = 1;
          that.setTransform(-that.currentIndex * that.imgWidth); //
        } else if (that.currentIndex <= 0) {
          that.currentIndex = that.slideCount;
          that.setTransform(-that.currentIndex * that.imgWidth);
        }
      }, that.animalDuration);
    },
    touchStart: function(e) {
      //
      if (this.scrolling) return;
      //
      this.stopTimer();
      this.startX = e.touches[0].pageX; //
    },
    touchMove: function(e) {
      this.currentX = e.touches[0].pageX;
      this.distance = this.currentX - this.startX;
      let currentPosition = -this.currentIndex * this.imgWidth;
      let moveDistance = this.distance + currentPosition;
      this.setTransform(moveDistance);
    },
    touchEnd: function(e) {
      let endDistance = Math.abs(this.distance); //
      let movePercentage = this.imgWidth * this.moveRatio; //
      if (this.distance === 0) {
        return;
      } else if (this.distance > 0 && endDistance > movePercentage) {
        //this.distance>0 
        this.currentIndex--;
      } else {
        this.currentIndex++;
      }
      this.scrollContent(-this.currentIndex * this.imgWidth);
      this.startTimer();
    },
    stopTimer: function() {
      clearInterval(this.playTimer);
    }
  },
  mounted: function() {
    //1.domslide
    setTimeout(() => {
      this.handleDom();
      this.startTimer();
    }, 200);
  }
}
</script>

<style scoped>
* {
  margin: 0px;
  padding: 0px;
}
#mySwiper {
  overflow: hidden;
  position: relative;
}

.swiper {
  display: flex;
}
.indicator {
  display: flex;
  position: absolute;
  justify-content: center;
  bottom: 12px;
  width: 100%;
}
.indicatorItem {
  width: 8px;
  height: 8px;
  margin: 5px 3px;
  border-radius: 50%;
  background-color: #fff;
}
.active {
  background-color: rgba(212, 62, 46, 1);
}
</style>
