<template>
    <div class="wrapper" ref="wrapper">
        <div class="content">
            <slot></slot>
        </div>
    </div>
</template>

<script>
    import BScroll from "better-scroll";

    export default {
        name: "Scroll",
        props: {
            probeType: {
                type: Number,
                default: 0,
            },
            pullUpLoad: {
                type: Boolean,
                default: false,
            },
        },
        data() {
            return {
                scroll: null,
            };
        },
        mounted() {
            //1.BScroll
            this.scroll = new BScroll(this.$refs.wrapper, {
                probeType: this.probeType,
                pullUpLoad: this.pullUpLoad,
                //refresh()
                observeDOM: true,
                observeImage: true,
                // clickfalse buttondiv
                click: true,
                top: true,
                //
                mouseWheel: true,
            });
            //2. scroll position
            if (this.probeType === 2 || this.probeType === 3) {
                this.scroll.on("scroll", (position) => {
                    this.$emit("scroll", position);
                });
            }
            //3.scroll
            if (this.pullUpLoad) {
                this.scroll.on("pullingUp", () => {
                    this.$emit("pullingUp");
                });
            }
        },
        created() {
            this.$nextTick(() => {
                if (!this.scroll) {
                    this.scroll = new BScroll(this.$refs.wrapper, {
                        scrollX: true,
                        eventPassthrough: "vertical",
                    });
                }
            });
        },
        methods: {
            //
            scrollTo(x, y, time = 300) {
                this.scroll &&
                    this.scroll.scrollTo &&
                    this.scroll.scrollTo(x, y, time);
            },
            //scrollfinishPullUp(),scroll
            finishpullup() {
                this.scroll && this.scroll.finishPullUp();
            },
            getScrollY() {
                return this.scroll ? this.scroll.y : 0;
            },
        },
    };
</script>

